export class Publisher {
    constructor() {
        this.watcherList = [];
    }
    addWatcher(watcher) {
        this.watcherList.push(watcher);
    }
    notify(oldVal, newVal) {
        this.watcherList.forEach(watcher => {
            watcher.update(oldVal, newVal);
        });
    }
}



